/* Navself css start */
.topNav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem 0;
  background-color: #f8f9fa;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
.topNavLeft {
  display: flex;
  align-items: center;
}
.topNavLeft h3{
    margin:0  1rem;
}
.topNavLeft small{
    margin:0  1rem;
}
.topAvatar {
  margin-left: 1rem;
}
.topNavRight {
  margin-right: 1rem;
}
.searchBtn{
    color: black;
    cursor: pointer;
}
/* Navself css end */

